      "en": [
        "[fix] fix wrong event param when value is zero #2209"
      ],
      "zh-CN": [
        "[fix] 修复值为 0 时事件参数为空的问题 #2209"
      ]
    },
    "v2.5.6": {
      "en": [
        "[fix] Fix json parsing issue for cancel menu #1782"
      ],
      "zh-CN": [
